/* raleway-300 - cyrillic_latin */
@font-face {
  font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */
  font-family: "Raleway";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/Raleway/raleway-v28-cyrillic_latin-300.eot"); /* IE9 Compat Modes */
  src: url("../fonts/Raleway/raleway-v28-cyrillic_latin-300.eot?#iefix")
      format("embedded-opentype"),
    /* IE6-IE8 */ url("../fonts/Raleway/raleway-v28-cyrillic_latin-300.woff2")
      format("woff2"),
    /* Super Modern Browsers */
      url("../fonts/Raleway/raleway-v28-cyrillic_latin-300.woff") format("woff"),
    /* Modern Browsers */
      url("../fonts/Raleway/raleway-v28-cyrillic_latin-300.ttf")
      format("truetype"),
    /* Safari, Android, iOS */
      url("../fonts/Raleway/raleway-v28-cyrillic_latin-300.svg#Raleway")
      format("svg"); /* Legacy iOS */
}
