#slideGallery {
  margin-bottom: 190px;
  .container {
    .item-wrap {
      padding: 20px;
      .item {
        position: relative;
        overflow: hidden;
        &:before {
          content: "";
          position: absolute;
          width: 100%;
          height: 100%;
          background-color: rgba(255, 150, 25, 0.5);
          backdrop-filter: blur(4px);
          transform: translateY(-103%);
          transition: all 0.3s ease;
        }
        &:hover:before {
          transform: translate(0);
        }
        &:hover i {
          transform: translateY(-70%) translatex(-50%);
        }
        i {
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translatex(-50%) translateY(-350%);
          font-size: 55px;
          color: #fff;
          transition: all 0.5s ease;
        }
        img {
          pointer-events: none;
        }
      }
    }
  }
  &.showRoom {
    margin-bottom: 110px;
  }
}

//RESPONSE

@media screen and (max-width: 430px) {
  .App {
    #slideGallery {
      .container {
        .item-wrap {
          padding: 20px 50px;
          .item {
            i {
              transform: translatex(-50%) translateY(-370%);
            }
            &:hover i {
              transform: translateY(-70%) translatex(-50%);
            }
          }
        }
      }
    }
  }
}
